@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.grid {
    width: var(--full-width);
    height: var(--full-width);
    border: 1px solid var(--blue);
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    min-width: 500px;
}
:root {
    --border-radius: 10px;
}

.square-path {
    background: var(--white);
}

.square {
    outline: 1px solid var(--blue);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5%;
    height: 5%;
}

.square-wall {
    transition-duration: 0.05s;
    background: var(--wall);
    outline: none;
}

.square-left {
    background: var(--purple);
    margin-right: auto;
    width: 75%;
    height: 50%;
    border-top-right-radius: 10px;
    border-top-right-radius: var(--border-radius);
    border-bottom-right-radius: 10px;
    border-bottom-right-radius: var(--border-radius);
}

.square-right {
    background: var(--purple);
    margin-left: auto;
    width: 75%;
    height: 50%;
    border-top-left-radius: 10px;
    border-top-left-radius: var(--border-radius);
    border-bottom-left-radius: 10px;
    border-bottom-left-radius: var(--border-radius);
}

.square-top {
    background: var(--purple);
    align-self: flex-start;
    width: 50%;
    height: 75%;
    border-bottom-left-radius: 10px;
    border-bottom-left-radius: var(--border-radius);
    border-bottom-right-radius: 10px;
    border-bottom-right-radius: var(--border-radius);
}

.square-bottom {
    background: var(--purple);
    align-self: flex-end;
    width: 50%;
    height: 75%;
    border-top-left-radius: 10px;
    border-top-left-radius: var(--border-radius);
    border-top-right-radius: 10px;
    border-top-right-radius: var(--border-radius);
}

.square-top-bottom {
    background: var(--purple);
    width: 50%;
    height: 100%;
}

.square-left-right {
    background: var(--purple);
    width: 100%;
    height: 50%;
}

.square-grid {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-rows: 25% 25% 25% 25%;
    grid-template-columns: 25% 25% 25% 25%;
}

.square-grid-right {
    grid-column-start: 4;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 4;
    background: var(--purple);
}

.square-grid-center {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 4;
    background: var(--purple);
}

.square-grid-left {
    grid-column-start: 0;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 4;
    background: var(--purple);
}

.square-grid-top {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 0;
    grid-row-end: 2;
    background: var(--purple);
}

.square-grid-bottom {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 4;
    grid-row-end: 5;
    background: var(--purple);
}

.square-image {
    width: 100%;
    height: 100%;
}
.button {
    font-family: 'Rubik', sans-serif;
    font-weight: bold;
    color: white;
    letter-spacing: 0.5px;
    border: none; 
    margin-right: 10px;
    box-shadow: 0px 0px 10px -2px black;
    text-shadow: 0px 0px 0.5px black;
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
    transition: -webkit-filter 0.5s ease;
    transition: filter 0.5s ease;
    transition: filter 0.5s ease, -webkit-filter 0.5s ease;
}

.button:hover {
    transition: -webkit-filter 0.5s ease;
    transition: filter 0.5s ease;
    transition: filter 0.5s ease, -webkit-filter 0.5s ease;
    -webkit-filter: brightness(80%);
            filter: brightness(80%);
}

.button-normal {
    height: 5vh;
    min-height: 45px;
    min-width: 100px;
    width: var(--button-width);
}

.button-mini {
    width: 60px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.no-solution-popup {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto auto auto auto;
    outline: 5px solid var(--red);
}
.popup {
    box-shadow: var(--popup-box-shadow);
    border-radius: 5px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: -webkit-min-content;
    height: min-content;
    background: white;
    width: 300px;
    font-size: 14px;
    position: absolute;
    z-index: 1;
}

.popup>Button {
    align-self: flex-end;
    margin-right: 0px;
}
.container {
    height: 90vh;
    width: 100vw;
    display: grid;
    grid-template-rows: -webkit-min-content auto;
    grid-template-rows: min-content auto;
    grid-row-gap: 10px;
    row-gap: 10px;
    justify-items: center;
    min-width: var(--min-app-width);
    position: relative;
}
.header {
    width: var(--full-width);
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    width: var(--full-width);
    justify-items: center;
    align-content: center;
    min-width: var(--min-app-width);
}
.infopanel {
    left: 50%;
    min-width: 100px;
    outline: 5px solid var(--blue);
}

.infopanel>span {
    align-self: center;
    font-size: 14px;
}

.infopanel>h4 {
    align-self: flex-start;
    margin-top: 0px;
}

.infopanel>Button {
    align-self: flex-end;
}
/* Variables */
:root {
    --full-width: 90vmin;
    --button-width: 20vmin;
    --white: #FFFFFF;
    --blue: #ADC6FF;
    --green: #0EAD69;
    --red: #D7816A;
    --purple: #2F6690;
    --violet: #553739;
    --wall: #DCC656;
    --min-app-width: 500px;
    --popup-box-shadow: 0px 0px 10px -1px black;
}

